.user-block {

  .user-link{
    justify-content:space-between;
    margin: 0 3px;
  }

  .logged-in{
    position: relative;

    button{
      text-align: left;
      border: none;
      background: #fff;

      .fa{
        margin: 0 5px -1px 5px;
      }

      .fa-chevron-up{
        display: none;
      }
    }

    button:focus{
      outline: none;
    }

    .dropdown-menu{
      display: none;
    }

    .open {
      .dropdown-menu{
        display: flex;
        flex-direction: column;
        position: absolute;
        z-index: 1000;
        top:26px;
        background: #fff;
        padding: 10px;
        width: 150px;
        min-width: 100px;
        line-height: 1.5em;
        border-bottom: solid 1px #ccc;
        border-left: solid 1px #ccc;
        border-right: solid 1px #ccc;
      }

      .fa-chevron-down{
        display: none;
      }

      .fa-chevron-up{
        display: inline-block;
      }
    }
  }

}

.my-bids {

  .page-header {
    text-align: center;
    font-size: 32px;
  }

  .chakra-accordion__button {
    background-color: #f1f1f1;
    padding: 0.5em 2em;
  }

  .h3-heading {
    font-weight: bold;
    font-size: 16px;
  }

  .chakra-table {
    width: auto;

    .edit-btn {
      color: #3182ce;

      .chakra-icon.css-onkibi {
        font-size: 16px;
        margin-left: 4px;
        margin-bottom: 4px;
      }
    }
  }

  .chakra-alert {
    width: auto;
    display: inline-flex;
  }

  .chakra-form-control {
    @media only screen and (min-width: 660px) {
      display: flex;
    }

    .chakra-form__label {
      margin-top: 1.7em;
    }

    .chakra-input {
      width: auto;
      @media only screen and (min-width: 660px) {
        margin: 1em 1em 0;
      }
      
    }
  }

  .popup {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -60%);
    background: #fff;
    padding: 1em 2em;
    border: solid 1px #ccc;
    box-shadow: #4e5b61 8px 8px 6px;

    .close-button {
      color: #3182ce;
      float: right;
      font-size: 14px;

      .chakra-icon.css-onkibi {
        font-size: 12px;
        margin-left: 4px;
        margin-bottom: 4px;
      }
    }

    .clear {
      clear: both;
    }
  }

  .bottom-text {
    font-size: 16px;
    padding: 0.5em 1em
  }
  
}